import React from 'react';
import {connect} from 'react-redux';
import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';
import {Div, Panel, Alert, Group, Button, Cell, Tabs, FormLayout, Input, Search, List, PanelHeader} from "@vkontakte/vkui";
import Icon24Write from '@vkontakte/icons/dist/24/write';
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";



class HomePanelBase extends React.Component {

    state = {
        showImg: false,
        value1: "",
        value2: "",
        res: "Вы еще не ввели значения для вычисления."
    };


onChanges = (e) => {
const { name, value } = e.currentTarget;
this.setState({ [name]: value });
}
    showImg = () => {
        this.setState({showImg: true});
    };

num = () => {
var result;
result = this.state.value1 / 22.4;
this.setState({res: "Хим.количество(n) = "  + result+ "моль"});
console.log(this.state.res);
}


    render() {
        const {id, setPage, withoutEpic} = this.props;
        return (
            <Panel id={id}>
                <PanelHeader>Формула</PanelHeader>
<Group title="Как посчитать?">
<Div>
Введите в поля ввода свои значения, и нажмите на кнопку "Вычислить".
</Div>
</Group>





<Group title="Вычислитель">
<Div>
<FormLayout> 
<Input
type="number"
top="Значение объема(V)"
name="value1"
pattern="[A-Za-z]{3}"
onChange={this.onChanges}
status={this.state.value1 ? 'valid' : 'default'}
maxlength="6"
onKeyDown={this.func}
/>





<Button size="xl" level="commerce" onClick={(this.num)}>Вычислить</Button>
</FormLayout>
</Div>			 
</Group>
<Group title="Результат">
<Div>{this.state.res}</Div>
</Group>
</Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);